"use client"

import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Progress } from "@/components/ui/progress"
import { TrendingUp, Target, UserCheck } from "lucide-react"

interface StatsCardProps {
  studentProfile: {
    current_level: number
    total_points: number
    board_rank: number
    current_streak: number
    overall_accuracy: number
  }
}

export function StatsCard({ studentProfile }: StatsCardProps) {
  const {
    current_level = 1,
    total_points = 0,
    board_rank = 0,
    current_streak = 0,
    overall_accuracy = 0,
  } = studentProfile || {}

  // Calculate XP to next level
  const xpForCurrentLevel = (current_level - 1) * 1000
  const xpForNextLevel = current_level * 1000
  const xpInCurrentLevel = total_points - xpForCurrentLevel
  const xpNeededForNext = xpForNextLevel - xpForCurrentLevel
  const progressPercentage = (xpInCurrentLevel / xpNeededForNext) * 100

  return (
    <Card>
      <CardHeader>
        <CardTitle>Academic Statistics</CardTitle>
        <CardDescription>Your performance at a glance.</CardDescription>
      </CardHeader>
      <CardContent className="space-y-6">
        {/* Level and XP */}
        <div>
          <div className="flex justify-between items-center mb-2">
            <h4 className="font-semibold">Level {current_level}</h4>
            <Badge variant="secondary">Total XP: {total_points.toLocaleString()}</Badge>
          </div>
          <Progress value={progressPercentage} className="w-full" />
          <p className="text-xs text-muted-foreground mt-2 text-right">
            {xpInCurrentLevel.toLocaleString()} / {xpNeededForNext.toLocaleString()} XP to Level {current_level + 1}
          </p>
        </div>

        {/* Other Stats */}
        <div className="grid grid-cols-3 gap-4 text-center">
          <div>
            <UserCheck className="w-6 h-6 mx-auto mb-1 text-primary" />
            <p className="text-xl font-bold">{board_rank > 0 ? `#${board_rank}`: 'N/A'}</p>
            <p className="text-xs text-muted-foreground">Rank</p>
          </div>
          <div>
            <TrendingUp className="w-6 h-6 mx-auto mb-1 text-amber-500" />
            <p className="text-xl font-bold">{current_streak} days</p>
            <p className="text-xs text-muted-foreground">Streak</p>
          </div>
          <div>
            <Target className="w-6 h-6 mx-auto mb-1 text-emerald-500" />
            <p className="text-xl font-bold">{overall_accuracy}%</p>
            <p className="text-xs text-muted-foreground">Accuracy</p>
          </div>
        </div>
      </CardContent>
    </Card>
  )
}
